.todolist {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 1em;
}
.app{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.card {
  font-family:  Helvetica, Arial, sans-serif;
  background-color: white;
  margin: 10px;
  color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  justify-conent: center;
  -webkit-box-shadow: 5px 10px 18px #888888;
          box-shadow: 5px 10px 18px #888888;
  max-width: 300px;
  height:100%;
  padding: 0.5em

}

.card:hover{
  color: black;
  opacity: 0.5;

}

@media (max-width: 580px) {
  .todolist {
    display: block;
  }
}



